// ProfilePage.jsx with editable username/email + follow system
"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import api from "@/lib/api";
import { useAuth } from "@/contexts/AuthContext";
import { Pencil } from "lucide-react";

export default function ProfilePage() {
  const { username } = useParams();
  const { user: currentUser } = useAuth();
  const [user, setUser] = useState(null);
  const [posts, setPosts] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [editingBio, setEditingBio] = useState(false);
  const [editingDetails, setEditingDetails] = useState(false);
  const [bioInput, setBioInput] = useState("");
  const [usernameInput, setUsernameInput] = useState("");
  const [emailInput, setEmailInput] = useState("");
  const [followerCount, setFollowerCount] = useState(0);
  const [isFollowing, setIsFollowing] = useState(false);

  const isOwnProfile = currentUser?.username === user?.username;

  useEffect(() => {
    if (!username) return;
    const fetchUserProfile = async () => {
      try {
        const res = await api.get(`/users/profile/${username}`);
        setUser(res.data.user);
        setBioInput(res.data.user.bio || "");
        setUsernameInput(res.data.user.username);
        setEmailInput(res.data.user.email);
        setPosts(res.data.posts || []);
        fetchFollowData(res.data.user.id);
      } catch (err) {
        console.error("Error fetching profile:", err);
      }
    };
    fetchUserProfile();
  }, [username]);

  const fetchFollowData = async (profileId) => {
    try {
      const res = await api.get(`/users/${profileId}/followers`);
      setFollowerCount(res.data.length);
      setIsFollowing(res.data.some((f) => f.id === currentUser?.id));
    } catch (err) {
      console.error("Error fetching followers:", err);
    }
  };

  const handleFollowToggle = async () => {
    if (!user) return;
    try {
      if (isFollowing) {
        await api.delete(`/users/${user.id}/follow`);
        setFollowerCount((prev) => prev - 1);
      } else {
        await api.post(`/users/${user.id}/follow`);
        setFollowerCount((prev) => prev + 1);
      }
      setIsFollowing(!isFollowing);
    } catch (err) {
      console.error("Follow/unfollow error:", err);
    }
  };

  const handleAvatarChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    const formData = new FormData();
    formData.append("avatar", file);
    setUploading(true);
    try {
      await api.put("/users/profile", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
      const refreshed = await api.get(`/users/profile/${username}`);
      setUser(refreshed.data.user);
      setPosts(refreshed.data.posts || []);
    } catch (err) {
      console.error("Avatar upload failed:", err);
    } finally {
      setUploading(false);
    }
  };

  const handleBioSave = async () => {
    try {
      await api.put("/users/profile", { bio: bioInput });
      const refreshed = await api.get(`/users/profile/${username}`);
      setUser(refreshed.data.user);
      setEditingBio(false);
    } catch (err) {
      console.error("Bio update failed:", err);
    }
  };

  const handleDetailsSave = async () => {
    try {
      await api.put("/users/profile", {
        username: usernameInput,
        email: emailInput,
      });
      setEditingDetails(false);
      location.reload();
    } catch (err) {
      console.error("Update failed:", err);
    }
  };

  if (!user)
    return (
      <div className="text-center mt-20 text-gray-300">Loading profile...</div>
    );

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <div className="flex flex-col sm:flex-row items-center sm:items-start gap-6 mb-8">
        <div className="relative w-28 h-28 group">
          <img
            src={user.avatar || "/default-avatar.png"}
            alt="Avatar"
            className="w-full h-full rounded-full object-cover border-4 border-white shadow-md"
          />
          {isOwnProfile && (
            <label className="absolute inset-0 flex items-center justify-center rounded-full bg-black/50 opacity-0 group-hover:opacity-100 cursor-pointer transition-opacity">
              <Pencil size={20} className="text-white" />
              <input
                type="file"
                accept="image/*"
                onChange={handleAvatarChange}
                className="hidden"
              />
            </label>
          )}
        </div>

        <div className="flex-1 text-center sm:text-left">
          {editingDetails ? (
            <div className="space-y-2 mb-2">
              <input
                type="text"
                value={usernameInput}
                onChange={(e) => setUsernameInput(e.target.value)}
                className="px-2 py-1 rounded bg-gray-800 border text-white w-full sm:w-2/3"
              />
              <input
                type="email"
                value={emailInput}
                onChange={(e) => setEmailInput(e.target.value)}
                className="px-2 py-1 rounded bg-gray-800 border text-white w-full sm:w-2/3"
              />
              <button
                onClick={handleDetailsSave}
                className="px-3 py-1 bg-blue-500 text-white rounded"
              >
                Save
              </button>
            </div>
          ) : (
            <>
              <h1 className="text-3xl font-bold text-blue-400 mb-1">
                @{user.username}
              </h1>
              <p className="text-gray-400 mb-2">{user.email}</p>
              <p className="text-gray-400 mb-2">
                Joined: {new Date(user.created_at).toLocaleDateString()}
              </p>
              {isOwnProfile && (
                <button
                  onClick={() => setEditingDetails(true)}
                  className="text-sm text-blue-400 underline"
                >
                  Edit Username / Email
                </button>
              )}
            </>
          )}

          <div className="text-white my-3">
            {editingBio ? (
              <div className="flex gap-2 items-center">
                <input
                  type="text"
                  value={bioInput}
                  onChange={(e) => setBioInput(e.target.value)}
                  className="px-2 py-1 rounded bg-gray-800 border text-white"
                />
                <button
                  className="px-2 py-1 bg-blue-500 text-white rounded"
                  onClick={handleBioSave}
                >
                  Save
                </button>
              </div>
            ) : (
              <div className="flex items-center gap-2 justify-center sm:justify-start">
                <p>{user.bio || (isOwnProfile ? "No bio added." : "")}</p>
                {isOwnProfile && (
                  <button onClick={() => setEditingBio(true)}>
                    <Pencil size={16} className="text-blue-400" />
                  </button>
                )}
              </div>
            )}
          </div>

          <div className="flex gap-6 justify-center sm:justify-start text-white items-center">
            <p>
              <span className="font-semibold">{posts.length}</span> Posts
            </p>
            <p>
              <span className="font-semibold">{followerCount}</span> Followers
            </p>
            <p>
              <span className="font-semibold">0</span> Following
            </p>
            {!isOwnProfile && (
              <button
                onClick={handleFollowToggle}
                className={`px-3 py-1 rounded text-sm font-semibold transition-colors duration-200 ${
                  isFollowing
                    ? "bg-white text-blue-600 border border-blue-600 hover:bg-gray-100"
                    : "bg-blue-500 text-white hover:bg-blue-600"
                }`}
              >
                {isFollowing ? "Unfollow" : "Follow"}
              </button>
            )}
          </div>
        </div>
      </div>

      <h2 className="text-2xl font-semibold mb-3 text-white">Posts</h2>
      {posts.length === 0 ? (
        <p className="text-gray-400">No posts yet.</p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {posts.map((post) => (
            <div
              key={post.id}
              className="bg-white rounded-lg p-4 shadow hover:scale-[1.01] transition-transform text-black"
            >
              {post.image_url && (
                <img
                  src={post.image_url}
                  alt="Post"
                  className="w-full h-60 object-cover mb-2 rounded"
                />
              )}
              <p className="mb-2">{post.content}</p>
              <p className="text-sm text-gray-600">{post.like_count} likes</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
